import React, { Component } from "react";
import logo from "./assets/WorldPay.png";
// import SelectComponent from "./SelectComponent";

export default class CheckoutForm extends Component {
  render() {
    return (
      <div className="container" style={styles.container}>
        <div id="header" style={styles.header}>
          <div className="wrapper" style={styles.wrapper}>
            <div id="logo">
              <img src={logo} style={{ width: "76px" }} alt="logo" />
            </div>
            <div id="cart" style={styles.cart}>
              <i className="fas fa-shopping-cart fa-1x" />
              <span style={{ marginLeft: "20px" }}>$ 1.00 USD</span>
            </div>
          </div>
        </div>
        <div id="title" style={styles.title}>
          <h2>Payment Checkout</h2>
          <p>We don't share your financial details.</p>
        </div>
        <div id="select" style={styles.select}>
          {/* <SelectComponent /> */}
        </div>
      </div>
    );
  }
}

const styles = {
  container: {
    display: "flex",
    flexFlow: "column",
    justifyContent: "center",
    textAlign: "center",
    margin: "0 auto",
    color: "#004789"
  },
  wrapper: {
    display: "flex",
    flexFlow: "row",
    justifyContent: "space-between",
    alignItems: "center",
    maxWidth: "90%",
    margin: "0 auto"
  },
  header: {
    backgroundColor: "#fff"
  },
  cart: {
  },
  title: {},
  select: {
    maxWidth: "70%",
    minWidth: "350px",
    margin: "20px auto"
  }
};



