import React, { Fragment } from "react";
import CardView from "./CardView";
import { validate } from "./utils/validator";
import { withStyles } from "@material-ui/core/styles";
import Button from "@material-ui/core/Button";
import Tooltip from "@material-ui/core/Tooltip";
import green from "@material-ui/core/colors/green";
import blue from "@material-ui/core/colors/blue";
// import IconButton from "@material-ui/core/IconButton";
import DoneIcon from "@material-ui/icons/DoneAll";
import AutorenewIcon from "@material-ui/icons/Autorenew";
import Messenger from "./Messenger";
import payform from "payform";

const buttonStyles = theme => ({
  button: {
    color: "grey",
    width: "1px",
    margin: "0px",
    "&$disabled": {
      background: "none",
      color: "transparent"
    }
  },
  rightIcon: {
    marginLeft: theme.spacing.unit
  },
  disabled: {},
  renew: {
    backgroundColor: blue[600],
    color: "#fff",
    border: "1px solid",
    borderColor: blue[700],
    boxShadow: "#000 2px 2px 8px -1px",
    "&:hover": {
      backgroundColor: blue[800],
      boxShadow: "#000 1px 1px 8px -1px"
    },
    "&:active": {
      boxShadow: "none"
    },
    "&$disabled": {
      background: "#ccc",
      color: "white",
      boxShadow: "none",
      borderColor: "#bbb"
    }
  },
  done: {
    backgroundColor: green[600],
    color: "#fff",
    border: "1px solid",
    borderColor: green[700],
    boxShadow: "#000 2px 2px 8px -1px",
    "&:hover": {
      backgroundColor: green[800],
      boxShadow: "#000 1px 1px 8px -1px"
    },
    "&:active": {
      boxShadow: "none"
    },
    "&$disabled": {
      background: "#ccc",
      color: "white",
      boxShadow: "none",
      borderColor: "#bbb"
    }
  }
});

class CreditCard extends React.Component {
  state = {
    number: {
      value: "",
      isValid: false
    },
    date: {
      value: "",
      isValid: false
    },
    cvv: {
      value: "",
      isValid: false
    },
    holder: {
      value: "",
      isValid: false
    },
    type: "",
    activeInput: "",
    isButtonsHidden: true
  };
  componentDidMount() {
    this.getInputHandler(0);
  }

  getInputHandler = val => {
    const slider = document.getElementById("inputWrapper");
    slider.style.transform = `translate(${val}px)`;
  };

  setInputValue = name => {
    const value = document.getElementById(`mui-${name}`).value;
    // let isValid = payform.validateCardNumber(value); // Luhn algorithm validation
    let cardType = payform.parseCardType(value); // card type obtaining
    console.log(cardType);

    let isValid = validate(name, value); // fake validation
    name === "number"
      ? this.setState({
          [name]: { ...this.state[name], value: value, isValid: isValid },
          type: payform.parseCardType(value),
          activeInput: "",
        })
      : this.setState({
          [name]: { ...this.state[name], value: value, isValid: isValid },
          activeInput: "",
        });
  };

  confirmHandler = () => {
    const card = document.querySelector(".card-container");
    card.style.transform =
      "rotateX(170deg) skew(10deg, -10deg) translate(-30px, 0px)";
    card.style.MozTransform =
      "rotateX(-170deg) skew(10deg, -10deg) translate(-30px, 0px)";
    setTimeout(() => (card.style.backgroundImage = ""), 600);
    card.style.backgroundColor = styles.colors.success;
    setTimeout(
      () => (card.style.backgroundImage = styles.gradients.success),
      800
    );
    this.getInputHandler(-400);
    setTimeout(
      () =>
        this.setState({
          number: { ...this.state.number, isValid: false },
          date: { ...this.state.date, isValid: false },
          holder: { ...this.state.holder, isValid: false },
          cvv: { ...this.state.cvv, isValid: false },
          activeInput: "",
          isButtonsHidden: true
        }),
      800
    );
    document.querySelector(".cover-container").style.display = "none";
    document.querySelectorAll("input").forEach(el => (el.disabled = "true"));
  };

  cancelAllHandler = () => {
    this.cardView.reverseHandler("rotateX(0deg)");

    const card = document.querySelector(".card-container");
    card.style.transform =
      "rotateX(180deg) skew(0deg, 0deg) translate(0px, 0px)";
    card.style.MozTransform =
      "rotateX(-180deg) skew(0deg, 0deg) translate(0px, 0px)";
    setTimeout(() => (card.style.backgroundImage = styles.gradients.main), 800);

    document
      .querySelectorAll(".field")
      .forEach(field =>
        field.addEventListener("click", this.cardView.listener)
      );
    document.querySelector(".cover-container").style.display = "flex";

    this.getInputHandler(0);

    this.setState({
      number: { ...this.state.number, value: "", isValid: false },
      date: { ...this.state.date, value: "", isValid: false },
      holder: { ...this.state.holder, value: "", isValid: false },
      cvv: { ...this.state.cvv, value: "", isValid: false },
      type: "",
      activeInput: "",
      isButtonsHidden: true
    });
  };

  setActiveInputHandler = inputName => {
    this.setState({ activeInput: inputName });
  };
  setInputFocus = inputName => {
    document.getElementById(`mui-${inputName}`).focus();
  };

  showButtons = () => {
    this.setState({
      isButtonsHidden: false
    });
  };

  render() {
    const { number, date, holder, cvv } = this.state;
    const { classes } = this.props;

    const renewButton = (
      <Tooltip title="You can reset all values and return to the start.">
        <span>
          <Button
            size="small"
            variant="flat"
            className={classes.renew}
            onClick={this.cancelAllHandler}
            disabled={
              number.value === "" &&
              date.value === "" &&
              holder.value === "" &&
              cvv.value === ""
            }
            classes={{
              root: classes.renew,
              disabled: classes.disabled
            }}
          >
            <AutorenewIcon className={classes.rightIcon} />
          </Button>
        </span>
      </Tooltip>
    );

    const doneButton = (
      <Tooltip title="You have to fill all the field titles.">
        <span>
          <Button
            size="small"
            variant="flat"
            className={classes.done}
            onClick={this.confirmHandler}
            disabled={
              !number.isValid ||
              !date.isValid ||
              !holder.isValid ||
              !cvv.isValid
            }
            classes={{
              root: classes.done,
              disabled: classes.disabled
            }}
          >
            <DoneIcon className={classes.rightIcon} />
          </Button>
        </span>
      </Tooltip>
    );

    return (
      <Fragment>
        <Messenger />
        <CardView
          state={this.state}
          onGetInput={this.getInputHandler}
          onSetActiveInput={this.setActiveInputHandler}
          setInputFocus={this.setInputFocus}
          showButtons={this.showButtons}
          ref={ref => (this.cardView = ref)}
          renewButton={renewButton}
          setInputValue={this.setInputValue}
        />
        <div style={styles.buttons}>
          {renewButton}
          {doneButton}
        </div>
      </Fragment>
    );
  }
}

export default withStyles(buttonStyles)(CreditCard);

const mainBG = "#0053a0";
const mainGlint = "#006ace";

const successBG = "#008c06";
const successGlint = "#00a508";

const cancelBG = "#3e3e3e";
const cancelGlint = "#525252";

const styles = {
  inputContainer: {
    width: "200px",
    height: "100px",
    textAlign: "center",
    overflow: "hidden"
  },
  inputWrapper: {
    display: "flex",
    justifyContent: "space-between",
    width: "1400px",
    height: "100px",
    transition: "transform 1s"
  },
  inputSection: {
    width: "200px"
  },
  buttons: {
    display: "flex",
    justifyContent: "space-around",
    alignItems: "center",
    margin: "0 auto",
    width: "300px",
    height: "100px"
  },
  colors: {
    success: `${successBG}`
  },
  gradients: {
    main: `linear-gradient(
      30deg,
      ${mainBG}, ${mainBG}, ${mainBG},
      ${mainGlint},
      ${mainBG}, ${mainBG}, ${mainBG})`,
    success: `linear-gradient(
      30deg,
      ${successBG}, ${successBG}, ${successBG},
      ${successGlint},
      ${successBG}, ${successBG}, ${successBG})`,
    cancel: `linear-gradient(
      30deg, 
      ${cancelBG}, ${cancelBG}, ${cancelBG},
      ${cancelGlint},
      ${cancelBG}, ${cancelBG}, ${cancelBG})`
  }
};
