import React from "react";

const messenger = props => (
  <div style={styles.inputContainer} id="inputContainer">
    <div id="inputWrapper" style={styles.inputWrapper}>
      {/* Renew Section*/}
      <div className="renew-section" style={styles.inputSection}>
        <p>Click on the card to activate</p>
      </div>

      {/* Start Section*/}
      <div className="start-section" style={styles.inputSection}>
        <p>Click on the field titles to enter a value</p>
      </div>

      {/* Success Section*/}
      <div className="success-section" style={styles.inputSection}>
        <p>Well done!</p>
      </div>
    </div>
  </div>
);

const styles = {
  inputWrapper: {
    display: "flex",
    justifyContent: "space-between",
    width: "600px",
    height: "50px",
    // backgroundColor: "#ccc",
    transition: "transform 1s"
  },
  inputContainer: {
    width: "200px",
    height: "50px",
    margin: "0 auto",
    textAlign: "center",
    // border: "1px solid #aaa",
    overflow: "hidden"
  },
  inputSection: {
    width: "200px"
  },
}

export default messenger;



