export const validate = (inputName, value) => {
  
  switch (inputName) {
    case "number":
      return /^\d{4}\s\d{4}\s\d{4}\s\d{4}$/.test(value);
    case "date":
      return /^\d{2}\s\/\s\d{2}$/.test(value);
    case "holder":
    return /^[A-Za-z\s]{1,20}$/.test(value);
    case "cvv":
      return /^\d{3,4}$/.test(value);
    default:
      return false;
  }
};



