import payform from "payform";

export const format = inputName => {
  const input = document.getElementById(`mui-${inputName}`);

  switch (inputName) {
    case "number":
      return payform.cardNumberInput(input);
    case "date":
      return payform.expiryInput(input);
    case "holder":
      return;
    case "cvv":
      return payform.cvcInput(input);
    default:
      return payform.numericInput(input);
  }
};



