import React from "react";
import { withStyles } from "@material-ui/core/styles";
import Input from "@material-ui/core/Input";
// import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import ValidIcon from "@material-ui/icons/Done";
import { format } from "./utils/formatter";

const styles = theme => ({
  inputContainer: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "flex-start"
  },
  formControl: {
    // margin: theme.spacing.unit,
    display: "flex",
    flexFlow: "row",
    justifyContent: "space-between",
    alignItems: "flex-end"
  },
  cssLabel: {
    fontSize: "1rem",
    "&$cssFocused": {
      color: "#fff",
      fontSize: "1rem"
    }
  },
  cssFocused: {},
  cssInput: {
    display: "none",
    // fontSize: "1.2rem",
    color: "gold",
    fontWeight: "600",
    textShadow: "#000 0px 0px 5px"
    // textAlign: "center",
  },
  cssUnderline: {
    "&:before": {
      content: "none",
      borderBottomColor: "#fff"
    },
    "&:after": {
      content: "",
      borderBottomWidth: "2px",
      borderBottomColor: "gold"
    }
  },
  rightIcon: {
    marginLeft: theme.spacing.unit
  },
  rightIconPassive: {
    marginLeft: theme.spacing.unit,
    color: "#707070"
  }
});

class InputMUI extends React.Component {
  state = {
    name: ""
  };

  handleChange = event => {
    this.setState({ name: event.target.value });
  };

  render() {
    const {
      classes,
      nameObj: { value, isValid },
      type,
      name,
      id,
      setInputValue,
      width,
      textSize,
      activeInput
    } = this.props;

    return (
      <div className={classes.inputContainer}>
        <FormControl className={classes.formControl} style={{ width }}>
          {/* <InputLabel
            htmlFor={id}
            FormLabelClasses={{
              root: classes.cssLabel,
              focused: classes.cssFocused
            }}
          >
            {label}
          </InputLabel> */}
          <Input
            id={id}
            classes={{
              underline: classes.cssUnderline,
              input: classes.cssInput
            }}
            style={textSize}
            type={type}
            name={name}
            // value={value}
            onFocus={() => format(name)}
            onBlur={() => setInputValue(name)}
            fullWidth
          />
        </FormControl>
        {isValid && <ValidIcon className={classes.rightIcon} />}
      </div>
    );
  }
}

export default withStyles(styles)(InputMUI);



