import React, { Component, Fragment } from "react";
import Field from "./Field";

export default class CardView extends Component {
  componentDidMount() {
    document
      .querySelectorAll(".field")
      .forEach(field => field.addEventListener("click", this.listener));
  }

  listener = event => {
    const inputName = event.target.name || event.target.align;
    this.setInputValue(inputName);
  };

  setInputValue = inputName => {
    this.props.onSetActiveInput(inputName);
    setTimeout(() => this.props.setInputFocus(inputName, 700));
    document.getElementById(`mui-${inputName}`).style.display = "flex";
  };

  reverseHandler = value => {
    document.querySelector(".shell-container").style.transform = value;
    document.querySelector(".shell-container").style.MozTransform = value;
    // this.props.onGetInput(-200);
  };
  render() {
    const { activeInput, type } = this.props.state;
    const stateArray = Object.entries(this.props.state);
    return (
      <Fragment>
        <div className="shell-container" style={styles.shellContainer}>
          <div
            className="cover-container"
            onClick={() => this.reverseHandler("rotateX(180deg)")}
            style={{ ...styles.coverContainer }}
          >
            <div className="line line-1" style={styles.line}>
              <div>
                <div style={styles.mockLarge}>XXXX XXXX XXXX XXXX</div>
              </div>
              <div className="card-logo-mock">
                <i className="far fa-credit-card fa-2x" style={styles.logo} />
              </div>
            </div>
            <div className="line line-2" style={styles.line}>
              <div>
                <div style={styles.mockSmall}>MM/YY</div>
              </div>
              <div>
                <div style={styles.mockSmall}>CVV CODE</div>
              </div>
            </div>
            <div className="line line-3" style={styles.line}>
              <div>
                <div style={styles.mockLarge}>CARD HOLDER</div>
              </div>
            </div>
          </div>
          <div className="card-container" style={{ ...styles.cardContainer }}>
            <div className="line line-1" style={styles.line}>
              <Field
                inputEntry={stateArray[0]}
                activeInput={activeInput}
                mockSize={styles.mockLarge}
                textSize={styles.cardLargeText}
                width="210px"
                setInputValue={this.props.setInputValue}
              >
                XXXX XXXX XXXX XXXX
              </Field>

              <div className="card-logo">
                {type ? (
                  <i className={`fab fa-cc-${type} fa-2x`} style={styles.logo}/>
                ) : (
                  <i className="far fa-credit-card fa-2x" style={styles.mockLogo} />
                )}
              </div>
            </div>
            <div className="line line-2" style={styles.line}>
              <Field
                inputEntry={stateArray[1]}
                activeInput={activeInput}
                mockSize={styles.mockSmall}
                textSize={styles.cardSmallText}
                width= "80px"
                setInputValue={this.props.setInputValue}
              >
                MM/YY
              </Field>
              <Field
                inputEntry={stateArray[2]}
                activeInput={activeInput}
                mockSize={styles.mockSmall}
                textSize={styles.cardSmallText}
                width="60px"
                setInputValue={this.props.setInputValue}
              >
                CVV CODE
              </Field>
            </div>
            <div className="line line-3" style={styles.line}>
              <Field
                inputEntry={stateArray[3]}
                activeInput={activeInput}
                mockSize={styles.mockLarge}
                textSize={styles.cardLargeText}
                width="250px"
                setInputValue={this.props.setInputValue}
              >
                CARD HOLDER
              </Field>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const mainBG = "#0053a0";
const mainGlint = "#006ace";

const coverBG = "#3e3e3e";
const coverGlint = "#525252";

const styles = {
  shellContainer: {
    position: "relative",
    width: "340px",
    height: "215px",
    margin: "20px auto",
    transformStyle: "preserve-3d",
    transition: "transform 1s ease",
    MozTransformStyle: "preserve-3d",
    MozTransition: "transform 1s ease"
  },
  coverContainer: {
    position: "absolute",
    width: "100%",
    height: "100%",
    backfaceVisibility: "hidden",
    MozBackfaceVisibility: "hidden",

    display: "flex",
    flexFlow: "column",
    justifyContent: "space-evenly",
    alignItems: "center",
    backgroundColor: coverBG,
    backgroundImage: `linear-gradient(30deg,
                      ${coverBG}, ${coverBG}, ${coverBG},
                      ${coverGlint},
                      ${coverBG}, ${coverBG}, ${coverBG})`,
    color: "#bbb",
    fontWeight: "600",
    boxShadow: "#000 0px 0px 5px 0px",
    borderRadius: "12px"
  },
  cardContainer: {
    position: "absolute",
    width: "100%",
    height: "100%",
    backfaceVisibility: "hidden",
    MozBackfaceVisibility: "hidden",
    transform: "rotateX(180deg)",
    MozTransform: "rotateX(180deg)",

    display: "flex",
    flexFlow: "column",
    justifyContent: "space-evenly",
    alignItems: "center",
    backgroundColor: mainBG,
    backgroundImage: `linear-gradient(30deg,
                      ${mainBG}, ${mainBG}, ${mainBG},
                      ${mainGlint},
                      ${mainBG}, ${mainBG}, ${mainBG})`,
    color: "gold",
    fontWeight: "600",
    boxShadow: "#000 0px 0px 5px 0px",
    borderRadius: "12px",
    transition: "transform 2s, background-color 2s",
    MozTransition: "transform 2s, background-color 2s"
  },
  mockSmall: {
    fontFamily: "monospace",
    fontSize: "0.7rem",
    color: "#bbb"
  },
  mockLarge: {
    fontSize: "1.1rem",
    color: "#bbb"
  },
  logo: {
    boxShadow: "#000 2px -1px 11px -2px"
  },
  mockLogo: {
    color: "#bbb",
  },
  line: {
    display: "flex",
    flexFlow: "row",
    justifyContent: "space-between",
    width: "85%"
  },
  cardSmallText: {
    fontSize: "0.9rem",
    textShadow: "#000 0px 0px 5px"
  },
  cardLargeText: {
    fontSize: "1.2rem",
    textShadow: "#000 0px 0px 5px"
  }
};



