import React from "react";
import InputMUI from "./InputMUI";

const Field = props => {
  const {
    inputEntry: [name, nameObj],
    activeInput,
    mockSize,
    textSize,
    width,
    setInputValue,
    children
  } = props;
  return (
    <div id={`${name}Field`} className={`${name} field -400`}>
      {nameObj.value === "" && activeInput !== name ? (
        <div className="mock" id={`mock-${name}`} align={name} style={mockSize}>
          {children}
        </div>
      ) : (
        <InputMUI
          nameObj={nameObj}
          id={`mui-${name}`}
          setInputValue={setInputValue}
          type="search"
          name={name}
          width={width}
          textSize={textSize}
          activeInput={activeInput}
          // label={children}
        />
      )}
    </div>
  );
};

export default Field;



